import { create } from 'zustand';

// API sync helper
async function syncSettingsToAPI<TState>(
  apiEndpoint: string | undefined, 
  data: TState
): Promise<boolean> {
  if (!apiEndpoint) return true;
  
  try {
    const response = await fetch(apiEndpoint, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      credentials: 'include',
      body: JSON.stringify(data),
    });
    return response.ok;
  } catch (error) {
    console.error(`[Settings Config API] sync error for ${apiEndpoint}:`, error);
    return false;
  }
}

export type SettingsConfigStoreState<TState> = {
  data: TState;
  initialized: boolean;
  setSection: <K extends keyof TState>(
    key: K,
    value: TState[K] | ((previous: TState[K]) => TState[K])
  ) => void;
  setData: (updater: (previous: TState) => TState) => void;
  resetSection: <K extends keyof TState>(key: K) => void;
  resetAll: () => void;
  loadFromAPI: () => Promise<void>;
};

export type SettingsConfigStore<TState> = ReturnType<typeof createSettingsConfigStore<TState>>;

interface SettingsConfigStoreOptions<TState> {
  storageKey: string;
  getDefaultState: () => TState;
  apiEndpoint?: string; // ✅ API endpoint for database sync
}

export function createSettingsConfigStore<TState>({
  storageKey,
  getDefaultState,
  apiEndpoint,
}: SettingsConfigStoreOptions<TState>) {
  return create<SettingsConfigStoreState<TState>>()(
    (set, get) => ({
        data: getDefaultState(),
        initialized: false,
        setSection: (key, value) => {
          const current = get().data;
          const nextValue =
            typeof value === 'function'
              ? (value as (previous: TState[typeof key]) => TState[typeof key])(current[key])
              : value;

          const newData = {
            ...current,
            [key]: nextValue,
          };
          
          set({ data: newData });
          
          // Sync to API in background
          syncSettingsToAPI(apiEndpoint, newData).catch(console.error);
        },
        setData: (updater) => {
          const newData = updater(get().data);
          set({ data: newData });
          
          // Sync to API in background
          syncSettingsToAPI(apiEndpoint, newData).catch(console.error);
        },
        resetSection: (key) => {
          const defaults = getDefaultState();
          const newData = {
            ...get().data,
            [key]: defaults[key],
          };
          
          set({ data: newData });
          
          // Sync to API in background
          syncSettingsToAPI(apiEndpoint, newData).catch(console.error);
        },
        resetAll: () => {
          const defaults = getDefaultState();
          set({ data: defaults });
          
          // Sync to API in background
          syncSettingsToAPI(apiEndpoint, defaults).catch(console.error);
        },
        
        loadFromAPI: async () => {
          if (!apiEndpoint) return;
          if (get().initialized) return;
          
          try {
            const response = await fetch(apiEndpoint, {
              credentials: 'include',
            });
            if (response.ok) {
              const json = await response.json();
              const data = json.data || json;
              if (data && Object.keys(data).length > 0) {
                set({ 
                  data: { ...getDefaultState(), ...data },
                  initialized: true 
                });
              } else {
                set({ initialized: true });
              }
            }
          } catch (error) {
            console.error(`[Settings Config Store] loadFromAPI error for ${apiEndpoint}:`, error);
          }
        },
      })
  );
}
